.friends {
	background-color: rgb(93, 116, 109);
}
.friendsTitle {
	display: flex;
	flex-direction: row;
	justify-content: center;
}
.eachFriendWrap {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
}
.friendsItem {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
}
.friendsIcon {
	text-decoration: none;
	padding: 5px;
}
.friendsIcon img{
	border-radius: 50%;
	width: 30px;
	height: 30px;
}
